export default {
	data() {
		return {
			form:{
				userName: '',
				password: '',
				passwordRepeat:'',
				secondPassword:'',
				secondPasswordRepeat:''
			}
		};
	},
	computed:{
	   i18n() {  
	     return this.$i18nMsg()  
	   }
	},
	onLoad:function(){
		uni.setNavigationBarTitle({
		            title:this.$i18nMsg().page.createWallet
		});
	},
	methods:{
		hasCaseLetter(str) {
		    for (var i in str) {
		        var asc = str.charCodeAt(i);
		        if ((asc >= 65 && asc <= 90)) {
		            return true;
		        }
		    }
		    return false;
		},
		hasLetter(str) {
		    for (var i in str) {
		        var asc = str.charCodeAt(i);
		        if ((asc >= 65 && asc <= 90 || asc >= 97 && asc <= 122)) {
		            return true;
		        }
		    }
		    return false;
		},
		async submit(){
			 if (!this.hasLetter(this.form.userName)){
				 uni.showToast({
				 	title: '用戶名必須包含字母',
				 	icon: 'none'
				 });
				 return
			 }
			 if (this.form.userName.length<4 && this.form.userName.length>9){
				 uni.showToast({
				 	title: '用戶名長度為3~9位',
				 	icon: 'none'
				 });
				 return
			 }
			 if (this.form.password.length<8){
				 uni.showToast({
				 	title: '密碼不能少於8位',
				 	icon: 'none'
				 });
				 return
			 }
			 if (!this.hasCaseLetter(this.form.password)){
				 uni.showToast({
					title: '密碼必須包含大寫字母',
					icon: 'none'
				 });
				 return
			 }
			 
			 if (this.form.secondPassword.length<8){
				 uni.showToast({
					title: '支付密碼不能少於8位',
					icon: 'none'
				 });
				 return
			 }
			 if (!this.hasCaseLetter(this.form.secondPassword)){
					 uni.showToast({
						title: '支付密碼必須包含大寫字母',
						icon: 'none'
					 });
					 return
			 }
			 if (this.form.password !== this.form.passwordRepeat) {
				 uni.showToast({
				 	title: '登錄密碼不一致',
					icon: 'none'
				 });
				 return
			 }
			 if (this.form.secondPassword !== this.form.secondPasswordRepeat) {
				 uni.showToast({
					title: '支付密碼不一致',
					icon: 'none'
				 });
				 return
			 }
			 let data ={
				 userName: this.form.userName, //用户名
				 walletPassword: this.form.password, //	钱包密码
				 payPassword: this.form.secondPassword	//支付密码

			 }
			 const res = await this.$http.get('/user/sign_in',{data: data})
			 console.log(res)
			 
			 uni.navigateTo({
			 	url: `./zhujiciBack?userCode=${res.data.data.msg}`
			 	
			 });
			 
		}
	}
}
